export default "<template>\n  <div :id=\"eleId\" class=\"chinaMapCls\" />\n</template>\n\n<script>\n  import * as echarts from 'echarts';\n  import chinaMapJson from '@/views/EchartUnit/map/mapJson/china/index.json';\n  export default {\n    props: {\n      data: {\n        type: Array,\n        default: () => [\n          [{ name: '黑龙江', value: 5 }],\n          [{ name: '内蒙古', value: 3 }],\n          [{ name: '北京市', value: 20 }],\n          [{ name: '吉林', value: 3 }],\n          [{ name: '辽宁', value: 5 }],\n          [{ name: '河北', value: 6 }],\n          [{ name: '天津市', value: 5 }],\n          [{ name: '山西', value: 7 }],\n          [{ name: '陕西', value: 6 }],\n          [{ name: '甘肃', value: 5 }],\n          [{ name: '宁夏', value: 5 }],\n          [{ name: '青海', value: 7 }],\n          [{ name: '新疆', value: 3 }],\n          [{ name: '西藏', value: 3 }],\n          [{ name: '四川', value: 10 }],\n          [{ name: '重庆市', value: 9 }],\n          [{ name: '广东', value: 10 }],\n          [{ name: '山东', value: 6 }],\n          [{ name: '河南', value: 8 }],\n          [{ name: '江苏', value: 10 }],\n          [{ name: '安徽', value: 9 }],\n          [{ name: '湖北', value: 10 }],\n          [{ name: '浙江', value: 16 }],\n          [{ name: '福建', value: 9 }],\n          [{ name: '湖南', value: 14 }],\n          [{ name: '贵州', value: 8 }],\n          [{ name: '广西', value: 7 }],\n          [{ name: '海南', value: 7 }],\n          [{ name: '上海市', value: 17 }]\n        ]\n      }\n    },\n    data() {\n      return {\n        eleId: `eleId_${Math.random()\n          .toString()\n          .substring(3, 6) + Date.now().toString(36)}`\n      };\n    },\n    watch: {\n      data: {\n        handler: function() {\n          this.initEchart();\n        },\n        deep: true\n      }\n    },\n    mounted() {\n      this.$echarts = echarts;\n      this.$nextTick(() => {\n        this.initEchart();\n      }, 0);\n    },\n    methods: {\n      initEchart() {\n        //\n        var chinaGeoCoordMap = {\n          黑龙江: [127.9688, 45.368],\n          内蒙古: [110.3467, 41.4899],\n          吉林: [125.8154, 44.2584],\n          北京市: [116.4551, 40.2539],\n          辽宁: [123.1238, 42.1216],\n          河北: [114.4995, 38.1006],\n          天津市: [117.4219, 39.4189],\n          山西: [112.3352, 37.9413],\n          陕西: [109.1162, 34.2004],\n          甘肃: [103.5901, 36.3043],\n          宁夏: [106.3586, 38.1775],\n          青海: [101.4038, 36.8207],\n          新疆: [87.9236, 43.5883],\n          西藏: [91.11, 29.97],\n          四川: [103.9526, 30.7617],\n          重庆市: [108.384366, 30.439702],\n          山东: [117.1582, 36.8701],\n          河南: [113.4668, 34.6234],\n          // 江苏: [118.8062, 31.9208],\n          江苏: [120.0062, 33.5208], // 调整后的\n          安徽: [117.29, 32.0581],\n          湖北: [114.3896, 30.6628],\n          浙江: [119.5313, 29.8773],\n          福建: [119.4543, 25.9222],\n          江西: [116.0046, 28.6633],\n          湖南: [113.0823, 28.2568],\n          贵州: [106.6992, 26.7682],\n          云南: [102.9199, 25.4663],\n          广东: [113.12244, 23.009505],\n          广西: [108.479, 23.1152],\n          海南: [110.3893, 19.8516],\n          上海市: [121.4648, 31.2891]\n        };\n\n        var chinaDatas = this.data;\n\n        // 只显示 北京市、天津市、河北、江苏、安徽、浙江、上海市、广东、湖南\n        var showProvinceArr = ['北京市', '天津市', '河北', '江苏', '安徽', '浙江', '上海市', '广东', '湖南'];\n        function showProvinceFoo(name, params) {\n          if (showProvinceArr.includes(name)) {\n            return chinaDatas.find(v => v[0].name === params.data.name)[0].value;\n          } else {\n            return '';\n          }\n        }\n\n        var series = [];\n        [['湖南', chinaDatas]].forEach(function(item, i) {\n          // console.log('item', item);\n          series.push({\n            type: 'effectScatter',\n            coordinateSystem: 'geo',\n            zlevel: 2,\n            // rippleEffect: {\n            //   // 涟漪特效\n            //   period: 4, // 动画时间，值越小速度越快\n            //   brushType: 'stroke', // 波纹绘制方式 stroke, fill\n            //   scale: 10 // 波纹圆环最大限制，值越大波纹越大\n            // },\n            label: {\n              normal: {\n                show: true,\n                position: 'right', // 显示位置\n                offset: [5, 0], // 偏移设置\n                formatter: function(params) {\n                  // 圆环显示文字\n                  // return params.data.name + ' ' + chinaDatas.find((v) => v[0].name === params.data.name)[0].value\n                  // return params.data.name + ' ' + showProvinceFoo(params.data.name, params)\n                  return '标题';\n                },\n                fontSize: 12\n              },\n              emphasis: {\n                show: true\n              }\n            },\n            symbol: 'circle',\n            symbolSize: function(val) {\n              return 6; // 圆环大小\n            },\n            itemStyle: {\n              normal: {\n                show: false,\n                color: '#FFA54F'\n                // color: '#3bc7ff'\n              }\n            },\n            data: item[1].map(function(dataItem) {\n              // console.log('中国', dataItem);\n              return {\n                name: dataItem[0].name,\n                value: chinaGeoCoordMap[dataItem[0].name].concat([dataItem[0].value])\n              };\n            })\n          });\n        });\n\n        console.log('series', series);\n\n        var option = {\n          title: {\n            text: '单位：人',\n            top: 0\n            // textStyle: {\n            //   color: this.$store.state.app.echartStyle.titleColor, // 字体颜色\n            //   fontSize: this.$store.state.app.echartStyle.titleFontSize, // 字体大小\n            //   fontWeight: this.$store.state.app.echartStyle.titleFontWeight // 字体粗细\n            // }\n          },\n          // 地图位置调整\n          layoutCenter: ['50%', '70%'],\n          layoutSize: '100%',\n\n          tooltip: {\n            trigger: 'item',\n            // backgroundColor: 'rgba(166, 200, 76, 0.82)', /// 背景色\n            borderColor: '#FFFFCC', // 背景色\n            showDelay: 0,\n            hideDelay: 0,\n            enterable: true,\n            transitionDuration: 0,\n            extraCssText: 'z-index:100',\n            formatter: function(params, ticket, callback) {\n              // 根据业务自己拓展要显示的内容\n              var res = '';\n              var name = params.name;\n              // var value = params.value[params.seriesIndex + 1]\n              var value = chinaDatas.find(v => v[0].name === params.data.name)[0].value;\n              res = \"<span style='color:#fff;'>\" + name + '</span><br/>数据：' + value;\n              if (name) {\n                return res;\n              } else {\n                return '';\n              }\n            }\n          },\n          // backgroundColor: '#13297B',/// 背景色 （生效）\n          visualMap: {\n            // 图例值控制\n            min: 1,\n            max: 1000000,\n            calculable: true,\n            show: true,\n            color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#00eaff'], // 散点颜色\n            // color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#3bc7ff'], // 散点颜色\n            textStyle: {\n              color: '#fff'\n            }\n          },\n          geo: {\n            map: 'china',\n            zoom: 1.42,\n            label: {\n              emphasis: {\n                show: false\n              }\n            },\n            roam: true, // 是否允许缩放\n            itemStyle: {\n              normal: {\n                color: '#13297B', // 地图背景色\n                borderColor: '#00fcff', // 省市边界线00fcff 516a89\n                borderWidth: 0.5\n              },\n              emphasis: {\n                color: '#13297B' // 悬浮背景\n              }\n            }\n          },\n          series: series\n        };\n\n        // end\n        const myChart = this.$echarts.init(document.getElementById(this.eleId));\n        echarts.registerMap('china', chinaMapJson);\n        myChart.setOption(option);\n      }\n    }\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .chinaMapCls {\n    height: 100%;\n    // color:#ffde00\n  }\n</style>\n";