<template>
  <div :id="eleId" class="chinaMapCls" />
</template>

<script>
  import * as echarts from 'echarts';
  import chinaMapJson from '@/views/EchartUnit/map/mapJson/china/index.json';
  export default {
    props: {
      data: {
        type: Array,
        default: () => [
          [{ name: '黑龙江', value: 5 }],
          [{ name: '内蒙古', value: 3 }],
          [{ name: '北京市', value: 20 }],
          [{ name: '吉林', value: 3 }],
          [{ name: '辽宁', value: 5 }],
          [{ name: '河北', value: 6 }],
          [{ name: '天津市', value: 5 }],
          [{ name: '山西', value: 7 }],
          [{ name: '陕西', value: 6 }],
          [{ name: '甘肃', value: 5 }],
          [{ name: '宁夏', value: 5 }],
          [{ name: '青海', value: 7 }],
          [{ name: '新疆', value: 3 }],
          [{ name: '西藏', value: 3 }],
          [{ name: '四川', value: 10 }],
          [{ name: '重庆市', value: 9 }],
          [{ name: '广东', value: 10 }],
          [{ name: '山东', value: 6 }],
          [{ name: '河南', value: 8 }],
          [{ name: '江苏', value: 10 }],
          [{ name: '安徽', value: 9 }],
          [{ name: '湖北', value: 10 }],
          [{ name: '浙江', value: 16 }],
          [{ name: '福建', value: 9 }],
          [{ name: '湖南', value: 14 }],
          [{ name: '贵州', value: 8 }],
          [{ name: '广西', value: 7 }],
          [{ name: '海南', value: 7 }],
          [{ name: '上海市', value: 17 }]
        ]
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`
      };
    },
    watch: {
      data: {
        handler: function() {
          this.initEchart();
        },
        deep: true
      }
    },
    mounted() {
      this.$echarts = echarts;
      this.$nextTick(() => {
        this.initEchart();
      }, 0);
    },
    methods: {
      initEchart() {
        //
        var chinaGeoCoordMap = {
          黑龙江: [127.9688, 45.368],
          内蒙古: [110.3467, 41.4899],
          吉林: [125.8154, 44.2584],
          北京市: [116.4551, 40.2539],
          辽宁: [123.1238, 42.1216],
          河北: [114.4995, 38.1006],
          天津市: [117.4219, 39.4189],
          山西: [112.3352, 37.9413],
          陕西: [109.1162, 34.2004],
          甘肃: [103.5901, 36.3043],
          宁夏: [106.3586, 38.1775],
          青海: [101.4038, 36.8207],
          新疆: [87.9236, 43.5883],
          西藏: [91.11, 29.97],
          四川: [103.9526, 30.7617],
          重庆市: [108.384366, 30.439702],
          山东: [117.1582, 36.8701],
          河南: [113.4668, 34.6234],
          // 江苏: [118.8062, 31.9208],
          江苏: [120.0062, 33.5208], // 调整后的
          安徽: [117.29, 32.0581],
          湖北: [114.3896, 30.6628],
          浙江: [119.5313, 29.8773],
          福建: [119.4543, 25.9222],
          江西: [116.0046, 28.6633],
          湖南: [113.0823, 28.2568],
          贵州: [106.6992, 26.7682],
          云南: [102.9199, 25.4663],
          广东: [113.12244, 23.009505],
          广西: [108.479, 23.1152],
          海南: [110.3893, 19.8516],
          上海市: [121.4648, 31.2891]
        };

        var chinaDatas = this.data;

        // 只显示 北京市、天津市、河北、江苏、安徽、浙江、上海市、广东、湖南
        var showProvinceArr = ['北京市', '天津市', '河北', '江苏', '安徽', '浙江', '上海市', '广东', '湖南'];
        function showProvinceFoo(name, params) {
          if (showProvinceArr.includes(name)) {
            return chinaDatas.find(v => v[0].name === params.data.name)[0].value;
          } else {
            return '';
          }
        }

        var series = [];
        [['湖南', chinaDatas]].forEach(function(item, i) {
          // console.log('item', item);
          series.push({
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 2,
            // rippleEffect: {
            //   // 涟漪特效
            //   period: 4, // 动画时间，值越小速度越快
            //   brushType: 'stroke', // 波纹绘制方式 stroke, fill
            //   scale: 10 // 波纹圆环最大限制，值越大波纹越大
            // },
            label: {
              normal: {
                show: true,
                position: 'right', // 显示位置
                offset: [5, 0], // 偏移设置
                formatter: function(params) {
                  // 圆环显示文字
                  // return params.data.name + ' ' + chinaDatas.find((v) => v[0].name === params.data.name)[0].value
                  // return params.data.name + ' ' + showProvinceFoo(params.data.name, params)
                  return '标题';
                },
                fontSize: 12
              },
              emphasis: {
                show: true
              }
            },
            symbol: 'circle',
            symbolSize: function(val) {
              return 6; // 圆环大小
            },
            itemStyle: {
              normal: {
                show: false,
                color: '#FFA54F'
                // color: '#3bc7ff'
              }
            },
            data: item[1].map(function(dataItem) {
              // console.log('中国', dataItem);
              return {
                name: dataItem[0].name,
                value: chinaGeoCoordMap[dataItem[0].name].concat([dataItem[0].value])
              };
            })
          });
        });

        console.log('series', series);

        var option = {
          title: {
            text: '单位：人',
            top: 0
            // textStyle: {
            //   color: this.$store.state.app.echartStyle.titleColor, // 字体颜色
            //   fontSize: this.$store.state.app.echartStyle.titleFontSize, // 字体大小
            //   fontWeight: this.$store.state.app.echartStyle.titleFontWeight // 字体粗细
            // }
          },
          // 地图位置调整
          layoutCenter: ['50%', '70%'],
          layoutSize: '100%',

          tooltip: {
            trigger: 'item',
            // backgroundColor: 'rgba(166, 200, 76, 0.82)', /// 背景色
            borderColor: '#FFFFCC', // 背景色
            showDelay: 0,
            hideDelay: 0,
            enterable: true,
            transitionDuration: 0,
            extraCssText: 'z-index:100',
            formatter: function(params, ticket, callback) {
              // 根据业务自己拓展要显示的内容
              var res = '';
              var name = params.name;
              // var value = params.value[params.seriesIndex + 1]
              var value = chinaDatas.find(v => v[0].name === params.data.name)[0].value;
              res = "<span style='color:#fff;'>" + name + '</span><br/>数据：' + value;
              if (name) {
                return res;
              } else {
                return '';
              }
            }
          },
          // backgroundColor: '#13297B',/// 背景色 （生效）
          visualMap: {
            // 图例值控制
            min: 1,
            max: 1000000,
            calculable: true,
            show: true,
            color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#00eaff'], // 散点颜色
            // color: ['#f44336', '#fc9700', '#ffde00', '#ffde00', '#3bc7ff'], // 散点颜色
            textStyle: {
              color: '#fff'
            }
          },
          geo: {
            map: 'china',
            zoom: 1.42,
            label: {
              emphasis: {
                show: false
              }
            },
            roam: true, // 是否允许缩放
            itemStyle: {
              normal: {
                color: '#13297B', // 地图背景色
                borderColor: '#00fcff', // 省市边界线00fcff 516a89
                borderWidth: 0.5
              },
              emphasis: {
                color: '#13297B' // 悬浮背景
              }
            }
          },
          series: series
        };

        // end
        const myChart = this.$echarts.init(document.getElementById(this.eleId));
        echarts.registerMap('china', chinaMapJson);
        myChart.setOption(option);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .chinaMapCls {
    height: 100%;
    // color:#ffde00
  }
</style>
