<template>
  <div class="page">
    <!-- 1 -->
    <div class="header"><h3 class="title">中国地图</h3></div>
    <section class="units-box">
      <ul class="unit-list">
        <li class="unit-item" v-for="(item, i) in dynamicComponents_A" :key="i + 'A'" :id="'itemCls' + i">
          <div class="theUnit"><component :is="dynamicComponents_A[i]"></component></div>
          <div class="title" info="标题">{{ getInfoTip(vueFiles[`MapA${i + 1}`]) }}</div>
          <div class="btnBlock">
            <span class="iconfont icon-fuzhi" title="复制" v-clipboard:copy="vueFiles[`MapA${i + 1}`]" v-clipboard:success="clipboardSuccess"> </span>
            <span class="iconfont icon-bianji" title="编辑" @click="onHandleItem(`MapA${i + 1}`)"></span>
            <span class="iconfont icon-yulan" title="预览" @click="onPreview(`MapA${i + 1}`)"></span>
            <!-- <span class="iconfont icon-code-line" title="显示代码" @click="onTest(`MapA${i + 1}`)"></span> -->
          </div>
        </li>
      </ul>
    </section>
    <!-- 2 -->
    <div class="header"><h3 class="title">省地图</h3></div>
    <section class="units-box">
      <ul class="unit-list">
        <li class="unit-item" v-for="(item, i) in dynamicComponents_B" :key="i + 'B'" :id="'itemCls' + i">
          <div class="theUnit"><component :is="dynamicComponents_B[i]"></component></div>
          <div class="title" info="tip提示">{{ getInfoTip(vueFiles[`MapB${i + 1}`]) }}</div>
          <div class="btnBlock">
            <span class="iconfont icon-fuzhi" title="复制" v-clipboard:copy="vueFiles[`MapB${i + 1}`]" v-clipboard:success="clipboardSuccess"></span>
            <span class="iconfont icon-bianji" title="编辑" @click="onHandleItem(`MapB${i + 1}`)"></span>
            <span class="iconfont icon-yulan" title="预览" @click="onPreview(`MapB${i + 1}`)"></span>
            <span class="iconfont icon-code-line" title="显示代码" @click="getInfoTip(vueFiles[`MapB${i + 1}`])"></span>
          </div>
        </li>
      </ul>
    </section>
    <!-- 3 -->
    <div class="header"><h3 class="title">市地图</h3></div>
    <section class="units-box">
      <ul class="unit-list">
        <li class="unit-item" v-for="(item, i) in dynamicComponents_C" :key="i + 'C'" :id="'itemCls' + i">
          <div class="theUnit"><component :is="dynamicComponents_C[i]"></component></div>
          <div class="title" info="tip提示">{{ getInfoTip(vueFiles[`MapC${i + 1}`]) }}</div>
          <div class="btnBlock">
            <span class="iconfont icon-fuzhi" title="复制" v-clipboard:copy="vueFiles[`MapC${i + 1}`]" v-clipboard:success="clipboardSuccess"></span>
            <span class="iconfont icon-bianji" title="编辑" @click="onHandleItem(`MapC${i + 1}`)"></span>
            <span class="iconfont icon-yulan" title="预览" @click="onPreview(`MapC${i + 1}`)"></span>
            <!-- <span class="iconfont icon-code-line" title="显示代码"></span> -->
          </div>
        </li>
      </ul>
    </section>
    <!-- 4 -->
    <div class="header"><h3 class="title">区县地图</h3></div>
    <section class="units-box">
      <ul class="unit-list">
        <li class="unit-item" v-for="(item, i) in dynamicComponents_D" :key="i + 'D'" :id="'itemCls' + i">
          <div class="theUnit"><component :is="dynamicComponents_D[i]"></component></div>
          <div class="title" info="标题">{{ getInfoTip(vueFiles[`MapD${i + 1}`]) }}</div>
          <div class="btnBlock">
            <span class="iconfont icon-fuzhi" title="复制" v-clipboard:copy="vueFiles[`MapD${i + 1}`]" v-clipboard:success="clipboardSuccess"></span>
            <span class="iconfont icon-bianji" title="编辑" @click="onHandleItem(`MapD${i + 1}`)"></span>
            <span class="iconfont icon-yulan" title="预览" @click="onPreview(`MapD${i + 1}`)"></span>
            <!-- <span class="iconfont icon-code-line" title="显示代码"></span> -->
          </div>
        </li>
      </ul>
    </section>
    <!-- 组件 -->
    <FullScreenCodeNew v-if="isShowFullCode" :isshow.sync="isShowFullCode" :codecontent.sync="codecontent" :componentname="previewEchart"></FullScreenCodeNew>
    <!-- 预览 -->
    <FullScreenPreview v-if="isShowFullPreview" :isshow.sync="isShowFullPreview">
      <component :is="previewEchart"></component>
      <template slot="btn">
        <span class="btnicon" v-clipboard:copy="vueFiles[`${previewEchart}`]" v-clipboard:success="clipboardSuccess">复制</span>
      </template>
    </FullScreenPreview>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { pop } from '@/utils/pop/index.js';
  let vueFiles = {}; // components目录下的所有.vue 单文件原始内容放在这里
  let dynamicComponents_All = [];

  batchInit();
  function batchInit() {
    const files = require.context('./components/', true, /\.vue$/);
    files.keys().forEach(fileName => {
      // 批量注册组件（组件名就是 vue文件名）
      const componentConfig = files(fileName);
      const componentName = fileName.replace(/^\.\//, '').replace(/\.vue$/, '');
      Vue.component(componentName, componentConfig.default || componentConfig);
      // 批量引入.vue原始文件内容
      const _fileName = fileName.replace(/\.\//, '');
      const fileContent = require(`!!raw-loader!./components/${_fileName}`); // 批量获取.vue 单文件的内容，使用require
      const fileKey = _fileName.replace('.vue', '');
      vueFiles[fileKey] = fileContent.default;
      dynamicComponents_All.push(fileKey);
    });
  }

  import { compile } from 'vue-template-compiler';
  const compiler = require('vue-template-compiler');

  import App from '@/App.vue';

  export default {
    data() {
      return {
        isShowFullCode: false,
        isShowFullPreview: false,
        previewEchart: '', // 预览的echart 组件
        codecontent: 'hello world',
        //
        vueFiles: {},
        //
        dynamicComponents_A: ['MapA1'],
        dynamicComponents_B: ['MapB1'],
        dynamicComponents_C: ['MapC1'],
        dynamicComponents_D: ['MapD1'],
        dynamicComponents: ['MapA1']
      };
    },
    mounted() {
      this.vueFiles = vueFiles;
      //
      this.dynamicComponents_A = dynamicComponents_All.filter(val => val.includes('A'));
      this.dynamicComponents_B = dynamicComponents_All.filter(val => val.includes('B'));
      this.dynamicComponents_C = dynamicComponents_All.filter(val => val.includes('C'));
      this.dynamicComponents_D = dynamicComponents_All.filter(val => val.includes('D'));
      //
    },
    methods: {
      onHandleItem(key) {
        this.isShowFullCode = true;
        this.previewEchart = key;
        this.codecontent = vueFiles[key];
      },
      // 复制
      clipboardSuccess() {
        pop();
      },
      onPreview(str) {
        this.isShowFullPreview = true;
        this.previewEchart = str;
      },
      onTest(key) {
        console.log('compiler', compiler);
        const { render, staticRenderFns } = compile(vueFiles[key]);
        new Vue({
          el: '#app',
          render: h =>
            h(App, {
              render,
              staticRenderFns
            })
        });
      },
      getInfoTip(vueFileStr) {
        if (!vueFileStr) return;
        const nameRegex = /infoTip:\s*['"]([^'"]+)['"]/;
        if (!nameRegex) return;
        const nameMatch = vueFileStr.match(nameRegex);
        const nameValue = (nameMatch && nameMatch[1]) || '备注：';
        // console.log('name', nameValue);
        return nameValue;
      }
    }
  };
</script>

<style lang="scss" scoped>
  @import '@/views/EchartUnit/_comconfig/commonPage.scss';
</style>
